$(function() {


	$('.invest-slider__list').slick({
		slidesToShow: 3,
		slidesToScroll: 3,
		infinite: false,
		prevArrow: $('.invest-slider__arrow--prev'),
		nextArrow: $('.invest-slider__arrow--next')
	});

	makeHerorDots ();

	$('.hero__slider').slick({
		slidesToShow: 1,
		slidesToScroll: 1,
		autoplay: true,
		autoplaySpeed: 7000,
		fade: true,
		arrows: false,
		dots: false
	});

	$('.hero__slider .hero__slide').each(applyHeroBackground);

	$('.hero__slider').on('afterChange',  (e, slick, idx) =>{
		$('.hero__dots-item').eq(idx)
			.addClass('is-active')
			.siblings().removeClass('is-active');
	})


	function applyHeroBackground (idx, el) {
		console.log("el: ", el);
		let bg = $(el).css('backgroundImage');
		let target = $(el).find('.hero__tile-list');
		console.log("target: ", target);

		if(bg && target.length > 0) {
			$(target).css({
				backgroundImage: bg
			})
		}
	} 

	function newsTabHandler(e) {
		e.preventDefault();
		let target = $(this).attr('href');
		$(this).addClass('is-active').siblings().removeClass('is-active');
		$(target).addClass('is-active').siblings().removeClass('is-active');
	}

	function makeHerorDots () {
		let l = $('.hero__slider .hero__slide').length;

		if(l === 0) return;

		let container = $('<div />', {
			class: 'hero__dots'
		});

		function handler () {
			$('.hero__slider').slick('goTo', $(this).index())
		} 

		for (var i = 0; i < l; i++) {
			let link = $('<div />', {
				class: 'hero__dots-item',
				html: `<span>0${i + 1}</span><svg  viewBox="0 0 56 56"> <circle cx="28" cy="28" r="25" stroke="red" stroke-width="2" fill="none" /></svg>`
			});

			if(i === 0) {
				link.addClass('is-active')
			}

			link.on('click', handler)

			container.append(link)
		}

		$('.hero').append(container)
	}

	$('.js-select').SumoSelect({
		captionFormat: '{0} Выбрано',
		captionFormatAllSelected: '{0} Все выбраны'
	})


	$('.newstab-nav__item').on('click', newsTabHandler);
});

